.projectCard {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid rgba(139, 139, 139, 0.5);
  background: rgba(250, 250, 250, 0.6);
  border-radius: 0.7rem;
  margin: 1rem;
  width: 18rem;
  height: 22rem;
  padding: 1rem;
  box-shadow: 2px 5px 10px 0px #a7a7a7a6;
}

.projectCard img {
  width: 11rem;
  height: 11rem;
  border-radius: 50%;
}

.projectCard button {
  border-radius: 0.7rem;
  outline: none;
  margin: 1rem;
  height: 2.5rem;
  width: 10.2rem;
  color: #fff;
  background: linear-gradient(170deg, rgb(255, 255, 255), #789a9f);
  /* background: linear-gradient(-45deg,rgb(255, 0, 0),rgba(255, 255, 255, 0.411)); */

  box-shadow: 1px 1px 3px 0px #a7a7a7a6;
  font-size: 1.2rem;
  font-weight: 100;
  transition:  0.3s;
}

.projectCard button:hover {
  background: linear-gradient(160deg, rgb(255, 255, 255), #789a9f);
  font-weight: 400;
}
